import React, { useEffect, useState } from "react";
import { SubjectSlider } from "../Component/SubjectSlider";
import { CourseSlider } from "../Component/CourseSlider";
import { Course } from "../Component/commonComponent/Course";
import { useSelector } from "react-redux";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";

export const Linhvuc = (props) => {
    const [listCourse, setListCourse] = useState([]);
    const auth = useSelector((state) => state.userStatus.auth);
    
    const fetchCourse = async () => {
        const Courses = await Axios.get(`${nodeApiLink}/api/course/get/linhvuc/${props.match.params.linhvuc}`);
        console.log(Courses);
        setListCourse(Courses.data);
    };
    useEffect(() => {
        fetchCourse();
        return () => {
            //
        };
    }, [auth,props.match.params.linhvuc]);
    return (
        <div className="container-fluid">
            <div className="px-3">
                <div className="Popular-course">
                    <h3 className="px-3">Các khóa học phổ biến</h3>
                    <CourseSlider type="course" courses={listCourse?.length>0?listCourse.slice(0,11):[]}/>
                </div>
            </div>
            <div className="px-3">
                <div className="Popular-course">
                    <h3>Toàn bộ khóa học liên quan</h3>
                    <div className="row card-columns mt-4 pt-4 discovery-card-list">
                        {listCourse.map((course) => {
                            return <Course key={course._id} data={course} />;
                        })}
                    </div>
                </div>
            </div>
            <div className="px-3">
                <div className="subjects">
                    <h3>Khám phá</h3>
                    <SubjectSlider />
                </div>
            </div>
        </div>
    );
};
export default Linhvuc